    display:block;
    margin:0.4em auto;
   }
  } */

.bouncy{
  animation: bouncy 5s infinite linear;
  position:relative;
}

@keyframes bouncy {
  0% {top:0em;}
  40% {top:0em;}
  43% {top:-0.9em;}
  46% {top:0em;}
  48% {top:-0.4em;}
  50% {top:0em;}
  100% {top:0em;}
}

span {
  /* font-family: 'Big Shoulders Stencil Display', cursive;
  font-family: 'Big Shoulders Stencil Text', cursive; */
  margin: 0.5em;
  font-size: calc(1em + 1.5vw);
  line-height: 1em;
  display: block;
  max-width: 70vw;
  color: #efefef;
  /* letter-spacing: .009em; */
}
h1 {
  margin: 0.25em;
  color: #ff6363;
  background: -webkit-linear-gradient(#fc4a1a, #f7b733);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: inherit;
  font-family: inherit;
  margin: 0.25em;
  cursor: pointer;
}
.button:hover {
  color: #000000;
  background-color: #ffffff;
}
button:active ~ .invert {
  -webkit-filter: invert(1);
          filter: invert(1);
}
@media all and (max-width: 30em) {
   .button {
    display: block;
    margin: 0.4em auto;
  }
}

@keyframes quote {
  0% {
    transform: scale(1) rotate(10deg);
  }
  10% {
    transform: scale(0.85);
  }
  30% {
    transform: scale(1.08) rotate(-10deg);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: translateY(0);
  }
}
.quote {
  animation: spring 0.8s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  /* filter: drop-shadow(0px 0px 30px rgba(244, 244, 244, 0.5)); */
}

@keyframes spring {
  0% {
    transform: scale(1) rotate(10deg);
  }
  10% {
    transform: scale(0.85);
  }
  30% {
    transform: scale(1.08) rotate(-10deg);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes springNoRotate {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.85);
  }
  30% {
    transform: scale(1.08);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
.spring {
  animation: spring 0.8s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  /* filter: drop-shadow(0px 0px 30px rgba(244, 244, 244, 0.5)); */
}
.springNoRotate {
  animation: springNoRotate 1.5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
}
.compressed {
  transform: scale(0.9);
}

